@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,500&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0; }

* {
  font-family: 'Noto Sans KR', sans-serif;
  margin: 0; }

code, .Module-mainlogo {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  color: black !important; }

a {
  text-decoration: none !important; }

.Blank-xxsmall {
  padding-top: 1vh; }

.Blank-xsmall {
  padding-top: 5vh; }

.Blank-small {
  padding-top: 10vh; }

.Blank-medium {
  padding-top: 20vh; }

.Blank-large {
  padding-top: 30vh; }

.Text-xsmall {
  font-size: 1rem; }

.Text-small {
  font-size: 1.4rem; }

.Text-medium {
  font-size: 2rem; }

.Text-large, .Module-mainlogo {
  font-size: 3rem; }

.Text-xlarge {
  font-size: 5rem; }

.Text-xxlarge {
  font-size: 10rem; }

.Text-bold, .Module-mainlogo {
  font-weight: 900; }

.Text-light {
  font-weight: 400; }

.Style-flex {
  display: flex; }

.Style-center {
  text-align: center; }

.Style-overflow-hidden {
  overflow: hidden; }

.Align-vertical-middle {
  vertical-align: middle; }

.Background-header {
  background-color: #ffca12;
  padding-bottom: 75px; }

.Background-header-index {
  background-color: #ffca12;
  padding-top: 10px;
  padding-bottom: 10px; }

.Module-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0; }

.Module-mainlogo {
  color: black;
  padding-left: 10px;
  padding-top: 10px;
  float: left;
  -webkit-transition: color 0.1s ease-in-out;
  transition: color 0.1s ease-in-out; }

.Module-mainlogo:hover {
  color: #4d5c7c; }

.Module-mainlogo:active {
  color: #ffca12; }

.Module-loginbutton {
  float: right;
  font-size: 1.1rem;
  font-weight: 500;
  margin-top: 30px;
  margin-right: calc(5px + 1vw);
  color: black;
  -webkit-transition: color 0.1s ease-in-out;
  transition: color 0.1s ease-in-out; }

.Module-loginbutton:hover {
  color: #4d5c7c; }

.Module-loginbutton:active {
  color: #ffca12; }

.Module-header-index {
  margin-left: 1.2rem;
  color: black;
  text-decoration: none; }

.Module-notfound {
  font-weight: 900;
  -webkit-animation: notfound-animate 5s infinite;
          animation: notfound-animate 5s infinite; }

@-webkit-keyframes notfound-animate {
  0% {
    color: black;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    font-size: 8rem; }
  50% {
    color: #ffca12;
    font-size: calc(5rem + 11vw);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    font-size: 8rem;
    color: black;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg); } }

@keyframes notfound-animate {
  0% {
    color: black;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    font-size: 8rem; }
  50% {
    color: #ffca12;
    font-size: calc(5rem + 11vw);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    font-size: 8rem;
    color: black;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg); } }

.Module-notfoundmove {
  margin-top: 2rem;
  -webkit-animation: notfoundmove-animate 5s infinite;
          animation: notfoundmove-animate 5s infinite; }

@-webkit-keyframes notfoundmove-animate {
  0% {
    margin-top: 2rem; }
  50% {
    margin-top: 8rem; }
  100% {
    margin-top: 2rem; } }

@keyframes notfoundmove-animate {
  0% {
    margin-top: 2rem; }
  50% {
    margin-top: 8rem; }
  100% {
    margin-top: 2rem; } }

.Module-button {
  border: none;
  background: #404040;
  color: #ffffff !important;
  font-weight: 400;
  margin-top: 20px;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-transform: uppercase;
  border-radius: 6px;
  display: inline-block;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s; }

.Module-button:hover {
  color: #404040 !important;
  font-weight: 900 !important;
  letter-spacing: 6px;
  background: none;
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s; }

.Module-buttonstyle {
  align-items: center;
  vertical-align: center; }

.Module-input {
  margin-left: calc(50vw - 180px);
  margin-right: calc(50vw - 180px); }

.Module-errormessage {
  margin-top: 100px;
  margin-bottom: 100px;
  color: red;
  -webkit-animation: errormessage-animate 0.2s ease-in-out;
          animation: errormessage-animate 0.2s ease-in-out; }

@-webkit-keyframes errormessage-animate {
  0% {
    color: white; }
  100% {
    color: red; } }

@keyframes errormessage-animate {
  0% {
    color: white; }
  100% {
    color: red; } }

